<template>
	<view class="container">
		<swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000" class="swiper">
			<swiper-item>
				<view class="swiper-item">
					<image src="../../static/images/01.jpg" mode=""></image>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="swiper-item">
					<image src="../../static/images/02.jpg" mode=""></image>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="swiper-item">
					<image src="../../static/images/03.jpg" mode=""></image>
				</view>
			</swiper-item>
		</swiper>
		
		<view class="form">
			<input type="text" @input="input" />
			<button size="mini" type="default" v-on:click="toList">跳转</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				value:'',//输入框
			}
		},
		methods: {
			toList(){
				uni.navigateTo({
					url:'../list/list?id='+this.value
				})
			},
			input(e){
				this.value=e.detail.value
			}
		}
	}
</script>

<style>
	.container {
		font-size: 14px;
	}
	.swiper image{width: 100%;}
	.form button,.form input{float: left;}
	.form{margin: 20rpx;}
	.form input{border: 1rpx solid #ddd;padding: 4rpx;}
</style>
